import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		faIcon,
		uniPopup
	},
	data() {
		return {
			submitRes:'',
			showFileName:false,
			showFileImg:false,
			imgs:[],
			files:[],
			imgSrc:'',
			fileName:'',
			isAgree:false,
			birthday: null,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			collClass: 'collapse collapseHide',
			popupText:'',
			form: {
				'1-1':{label:'姓名',txt:'',isNeed:true},
				'1-2':{label:'姓名（拼音）',txt:'',isNeed:true},
				'1-3':{label:'微信号',txt:'',isNeed:true},
				'1-4':{txt:''},
				'1-5':{label:'邮寄地址',txt:'',isNeed:true},
				'1-6':{label:'电话号码',txt:'',isNeed:true},
				'1-7':{label:'性别',value:'M'},
				'1-8':{label:'生日',txt:'',isNeed:true},
				'1-9':{label:'年龄',txt:'',isNeed:true},
				'2-1':{value:'N'},
				'2-2':{txt:''},
				'2-3':{value:'N'},
				'2-4':{txt:''},
				'2-5':{value:'N'},
				'2-6':{txt:''},
				'2-7':{value:'N'},
				'2-8':{txt:''},
				'2-9':{value:'N'},
				'2-10':{value:'N'},
				'2-11':{txt:''},
				'2-12':{value:'N'},
				'2-13':{txt:''},
				'2-14':{txt:''},
				'2-15':{txt:''},
				'2-16':{txt:''}
			},
			radios: {
				flag: [{
					name: '没有',
					value: 'N'
				}, {
					name: '有',
					value: 'Y'
				}],
				isSmoke: [{
					name: '不吸',
					value: 'N'
				}, {
					name: '吸烟',
					value: 'Y'
				}],
				isDrink: [{
					name: '不喝',
					value: 'N'
				}, {
					name: '每天',
					value: 'D'
				}, {
					name: '偶尔',
					value: 'S'
				}],
				isPregnant: [{
					name: '没有',
					value: 'N'
				}, {
					name: '有',
					value: 'Y'
				}, {
					name: '不知道',
					value: 'D'
				}],
				isLactation: [{
					name: '不是',
					value: 'N'
				}, {
					name: '是',
					value: 'Y'
				}],
				sexs: [{
					name: '男',
					value: 'M'
				}, {
					name: '女',
					value: 'F'
				}]
			}
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		},
		showBackTop() {
			return this.old.scrollTop >= 300 ? true : false
		}
	},
	methods: {
		deleteFile(index){
			this.files.splice(index,1)
		},
		deleteImg(index){
			this.imgs.splice(index,1)
		},
		closePopup(){
		    this.$refs.popup.close()
		},
		changeIsAgree(){
			this.isAgree=!this.isAgree
		},
		submit(){
			let _this = this
			let submitFlag = false
			for (let i in _this.form) {
				if(_this.form[i].isNeed){
					if(_this.form[i].txt===''){
						_this.popupText = _this.form[i].label+'不能为空！'
						_this.$refs.popup.open()
						break
					}else{
						if(i==='1-9') {
							submitFlag = true
							break
						}
					}
				}
			}
			if(submitFlag){
				let param = {group:'q2',anser:[],fileName:[]} 
				for(let i in _this.form){
					if(_this.form[i].value!==undefined){
						param.anser.push({step:i,value:_this.form[i].value})
					}else{
						param.anser.push({step:i,txt:_this.form[i].txt})
					}
				}
				if(_this.imgs.length>0){
					let uploadArr = []
					uni.showLoading({
						mask:true,
					    title: '文件上传中'
					});
					_this.imgs.forEach(e=>{
						uploadArr.push(new Promise(function(resolve,reject){
							uni.uploadFile({
							    url: 'https://www.shengkunhealth.com/med/up',
							    filePath: e.path,
							    name: 'file',
							        success: (res) => {
									let data = JSON.parse(res.data)
									param.fileName.push(data.fileName)
									resolve('OK')
							    }
							})	
						})
					)})
					
					Promise.all(uploadArr).then(
						res=>{
							param.fileName = param.fileName.join(",")
							uni.request({
								method:'POST',
							    url: 'https://www.shengkunhealth.com/med/post', //仅为示例，并非真实接口地址。
							    data: param,
							    header: {
							        'content-type':'application/json'
							    },
							    success: (res) => {
									uni.hideLoading()
									uni.showToast({
									    title: '提交成功',
										duration: 2000
									});
							    }
							});	
						}
					)
				}else{
					let uploadArr = []
					uni.showLoading({
						mask:true,
					    title: '文件上传中'
					});
					_this.files.forEach(e=>{
						uploadArr.push(new Promise(function(resolve,reject){
							uni.uploadFile({
							    url: 'https://www.shengkunhealth.com/med/up',
							    filePath: e.path,
							    name: 'file',
							        success: (res) => {
									let data = JSON.parse(res.data)
									param.fileName.push(data.fileName)
									resolve('OK')
							    }
							})	
						})
					)})
					
					Promise.all(uploadArr).then(
						res=>{
							param.fileName = param.fileName.join(",")
							uni.request({
								method:'POST',
							    url: 'https://www.shengkunhealth.com/med/post', //仅为示例，并非真实接口地址。
							    data: param,
							    header: {
							        'content-type':'application/json'
							    },
							    success: (res) => {
									uni.hideLoading()
									if(res.statusCode===200){
										uni.showToast({
										    title: '提交成功',
											duration: 2000
										});
									}else{
										uni.showToast({
											icon:'none',
										    title: '提交失败',
											duration: 2000
										});
									}
							    }
							});	
						}
					)
				}
			}
		},
		birthdayChange(e) {
			let year = new Date()
			this.form['1-8'].txt = this.birthday = e.target.value
			this.form['1-9'].txt = year.getFullYear()-parseInt(this.birthday.slice(0,5))
		},
		toPage(name) {
			uni.redirectTo({
				url: `../${name}/${name}`
			})
		},
		toggleBtnCollapse() {
			const liCon = this.$refs.liCon
			if (this.collClass === 'collapse collapseHide') {
				this.collClass = 'collapse collapseShow'
			} else {
				this.collClass = 'collapse collapseHide'
			}
		},
		goTop(e) {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
			this.old.scrollTop = 0
		},
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		radioChange(e, name) {
			this.form[name].value = e.target.value
		},
		showChooseFile(){
			this.$refs.choosefile.open()
		},
		chooseLoaclFile(){
			let _this = this
			_this.imgs = []
			_this.files = []
			wx.chooseMessageFile({
			    count: 5, //默认9
			    type: 'file',
			    success: function (res) {
					_this.showFileImg = false
					_this.files = res.tempFiles
					_this.showFileName = true
			    }
			});
			_this.$refs.choosefile.close()
		},
		chooseLoaclImage(e){
			let _this = this
			_this.imgs = []
			_this.files = []
			uni.chooseImage({
			    count: 5, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: [e],
			    success: function (res) {
					_this.showFileName = false
					_this.imgs = res.tempFiles
					_this.showFileImg = true
			    }
			});
			_this.$refs.choosefile.close()
		}
	}
}
